@charset "UTF-8";
@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  text-align: center;
  display: block;
  color: white;
}
.button--accent {
  background-color: #68b738;
}
.button--accent:hover {
  background-color: #5eaa2f;
}
.button--accent:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  margin: 0;
}

/* файлы для индекса */
.container {
  min-width: 280px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    min-width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    min-width: 1220px;
    padding: 0 110px;
  }
}

.main-header {
  position: relative;
}
@media (min-width: 768px) {
  .main-header {
    padding: 5px 0 36px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 53px 0 32px;
    z-index: 1;
  }
}
@media (min-width: 1440px) {
  .main-header--position {
    position: absolute;
    padding: 0;
    top: 55px;
    right: 0;
    left: 0;
  }
}

.main-header__container {
  min-width: 280px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-header__container {
    min-width: 708px;
    padding: 0 30px;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-header__container {
    min-width: 1220px;
    padding: 0 110px;
  }
}

.main-header__logo {
  position: relative;
  padding: 10px 20px 13px;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: start;
  gap: 55px;
  cursor: pointer;
  height: fit-content;
}
.main-header__logo:hover, .main-header__logo:focus {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__logo {
    padding: 0;
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    width: 202px;
    height: 59px;
    padding-top: 2px;
  }
}

@media (min-width: 768px) {
  .main-header__logo-icon {
    padding-bottom: 5px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo-icon {
    padding-bottom: 0;
    padding-top: 2px;
  }
}

.main-header__logo-text {
  padding-top: 5px;
}
@media (min-width: 768px) {
  .main-header__logo-text {
    display: none;
  }
}

.main-header__toggle {
  position: absolute;
  right: 20px;
  align-self: center;
  margin-left: auto;
  border: none;
  background-color: inherit;
  cursor: pointer;
  padding: 0;
  width: 24px;
  height: 20px;
}
.main-header__toggle::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  left: 0;
  background-image: url("../img/navigation_open.svg");
  width: 24px;
  height: 18px;
  top: 1px;
}
.main-header__toggle::after {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-image: url("../img/navigation_close.svg");
  width: 24px;
  height: 20px;
  left: 2px;
  top: 0;
  display: none;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle:hover {
  opacity: 0.8;
}
.main-header__toggle:active {
  opacity: 0.6;
}
.main-header__toggle:focus {
  outline: 2px solid #68b738;
  outline-offset: 5px;
}

.main-header__nav {
  display: flex;
  width: 100%;
}
@media (min-width: 768px) {
  .main-header__nav {
    width: auto;
  }
}

.main-header__nav-list {
  position: absolute;
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
  z-index: 2;
  top: 65px;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .main-header__nav-list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
    column-gap: 33px;
    margin: 0;
  }
}

.main-header__nav-item {
  display: flex;
  position: relative;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
  border-bottom: 1px solid #e6e6e6;
  margin: 0;
  background-color: white;
  height: 66px;
  min-width: 320px;
}
@media (min-width: 768px) {
  .main-header__nav-item {
    min-width: auto;
    height: auto;
    border: none;
  }
  .main-header__nav-item:nth-child(2) {
    margin-right: 5px;
  }
}
@media (min-width: 1440px) {
  .main-header__nav-item {
    background-color: transparent;
    margin-top: 0;
  }
  .main-header__nav-item:nth-child(2) {
    margin-right: 2px;
  }
}
.main-header__nav-item:first-child {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-header__nav-item:first-child {
    border: none;
  }
}

.main-header__nav-link {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  padding: 10px 0;
  align-self: center;
}
@media (min-width: 768px) {
  .main-header__nav-link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    padding-top: 8px;
  }
  .main-header__nav-link--current::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68b738;
    bottom: 3px;
    right: 0;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main-header__nav-link--current::after {
    bottom: 1px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-header__nav-link--current::after {
    bottom: -5px;
  }
}
@media (min-width: 1440px) {
  .main-header__nav-link--color {
    color: white;
  }
}
@media (min-width: 1440px) {
  .main-header__nav-link--current-desktop::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: white;
    bottom: -4px;
    right: 0;
  }
}
@media (min-width: 1440px) {
  .main-header__nav-link {
    font-size: 20px;
    line-height: 30px;
    padding-top: 1px;
    padding-bottom: 3px;
  }
}
.main-header__nav-link:hover {
  opacity: 0.6;
}
.main-header__nav-link:active {
  opacity: 0.6;
}
.main-header__nav-link:focus {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}

.main-header__nav--nojs + .main-header__toggle {
  display: none;
}

.main-header__nav--nojs .main-header__nav-list {
  position: static;
  display: block;
  margin-right: -20px;
}
@media (min-width: 768px) {
  .main-header__nav--nojs .main-header__nav-list {
    display: flex;
    margin: 0;
  }
}

.main-header__nav--closed .main-header__nav-list {
  display: none;
}
@media (min-width: 768px) {
  .main-header__nav--closed .main-header__nav-list {
    display: flex;
  }
}

.main-header__nav--opened .main-header__nav-list {
  position: absolute;
  display: block;
}
@media (min-width: 768px) {
  .main-header__nav--opened .main-header__nav-list {
    position: static;
    display: flex;
  }
}

.main-header__nav--closed ~ .main-header__toggle::after {
  display: none;
}

.main-header__nav--opened ~ .main-header__toggle::before {
  display: none;
}

.main-header__nav--opened ~ .main-header__toggle::after {
  display: block;
}

.programs {
  display: flex;
  flex-direction: column;
}

.programs__container {
  min-width: 280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .programs__container {
    min-width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .programs__container {
    display: grid;
    width: 1220px;
    gap: 80px;
    grid-template-columns: repeat(2, 570px);
    margin-bottom: 71px;
    margin-top: 80px;
  }
}

.programs__item {
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  height: fit-content;
  margin: 20px 0;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .programs__item {
    min-height: 372px;
    padding-right: 316px;
    padding-left: 52px;
    margin: 0;
    margin-bottom: 30px;
  }
  .programs__item:last-child {
    margin-bottom: 45px;
  }
}
@media (min-width: 1440px) {
  .programs__item {
    padding-left: 52px;
    margin: 0;
    padding-right: 61px;
    min-height: 374px;
  }
  .programs__item:last-child {
    margin-bottom: 0;
    margin-right: 0;
  }
}

.programs__title {
