  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  padding: 30px 0 29px 72px;
  position: relative;
}
.programs__title--icon-loss::before {
  content: "";
  position: absolute;
  top: 21px;
  left: 3px;
  width: 50px;
  height: 53px;
  background-image: url("../img/program_loss.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .programs__title--icon-loss::before {
    background-size: 200px 211px;
    width: 200px;
    height: 211px;
    top: 81px;
    left: unset;
    right: -254px;
  }
}
@media (min-width: 1440px) {
  .programs__title--icon-loss::before {
    background-size: 100px 106px;
    width: 100px;
    height: 106px;
    top: 41px;
    left: 0;
  }
}
.programs__title--icon-increase::before {
  content: "";
  position: absolute;
  top: 24px;
  left: -6px;
  width: 67px;
  height: 50px;
  background-image: url("../img/program_increase.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .programs__title--icon-increase::before {
    background-size: 268px 200px;
    width: 268px;
    height: 200px;
    top: 92px;
    left: unset;
    right: -288px;
  }
}
@media (min-width: 1440px) {
  .programs__title--icon-increase::before {
    background-size: 134px 100px;
    width: 134px;
    height: 100px;
    top: 47px;
    left: -19px;
  }
}
@media (min-width: 768px) {
  .programs__title {
    padding: 77px 0 24px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .programs__title {
    padding-bottom: 73px;
    padding-left: 162px;
  }
}

.programs__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  margin: 0;
  margin-bottom: 22px;
  padding-right: 45px;
  width: fit-content;
  position: relative;
}
.programs__link::after {
  position: absolute;
  content: "";
  height: 10px;
  width: 24px;
  top: 3px;
  right: 4px;
  background-image: url("../img/program_link_arrow.svg");
  background-size: 24px 10px;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .programs__link::after {
    top: 10px;
  }
}
.programs__link:hover::after {
  background-image: url("../img/program_link_arrow_hover.svg");
  width: 32px;
  background-size: 32px 10px;
}
.programs__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .programs__link {
    padding-right: 60px;
    margin-bottom: 53px;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .programs__link {
    margin-bottom: 55px;
  }
}

.programs__description {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin: 0;
  padding-bottom: 42px;
  position: relative;
}
.programs__description::after {
  content: "";
  position: absolute;
  height: 1px;
  bottom: 20px;
  left: 0;
  right: 0;
  background-color: #d9d9d9;
}
@media (min-width: 768px) {
  .programs__description::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .programs__description {
    padding-bottom: 53px;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .programs__description {
    padding-bottom: 30px;
  }
}

.hero {
  font-family: "Oswald", "Arial", sans-serif;
  color: white;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 63%, white 60%);
  position: relative;
  height: 100%;
}
@media (min-width: 768px) {
  .hero {
    background: none;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .hero {
    max-height: 696px;
    background-position: right 0;
    padding-top: 141px;
    padding-bottom: 2px;
  }
  .hero::after {
    position: absolute;
    content: "";
    background-color: rgba(104, 183, 56, 0.85);
    width: 50%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -2;
  }
  .hero::before {
    position: absolute;
    content: "";
    background-image: url("../img/hero_cat_xl.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right bottom;
    background-color: #4f4f54;
    width: 50%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -2;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .hero::before {
    background-image: url("../img/hero_cat_xl@2x.webp");
  }
}

.hero__container {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 302px;
}
@media (min-width: 768px) {
  .hero__container {
    align-items: flex-start;
  }
}
@media (min-width: 1440px) {
  .hero__container {
    align-items: flex-start;
    margin: 0 auto;
  }
}
.hero__container::after {
  content: "";
  position: absolute;
  width: 100%;
  min-height: 302px;
  display: block;
  background-image: url("../img/hero_cat.webp");
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -2;
  top: 0;
}
@media (min-resolution: 2dppx) {
  .hero__container::after {
    background-image: url("../img/hero_cat.png@2x.webp");
  }
}
@media (min-width: 768px) {
  .hero__container::after {
    background: none;
    width: fit-content;
  }
}

.hero__title {
  display: flex;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 0;
  padding: 27px 0 25px;
  z-index: 1;
}
@media (min-width: 768px) {
  .hero__title {
    color: black;
    padding: 23px 87px 21px;
    text-align: start;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1440px) {
  .hero__title {
    padding: 81px 80px 40px 80px;
    max-width: 420px;
  }
}

.hero__description {
  display: flex;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  padding-bottom: 29px;
  margin: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .hero__description {
    color: black;
    padding: 19px 88px 49px;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .hero__description {
    padding: 0 80px 52px 80px;
    max-width: 420px;
  }
}

.hero__image {
  display: block;
  background-color: transparent;
  padding-bottom: 3px;
}
@media (min-width: 768px) {
  .hero__image {
    position: absolute;
    top: 224px;
    padding: 0;
    width: 709px;
    min-height: 609px;
  }
}
@media (min-width: 1440px) {
  .hero__image {
    top: 0;
    left: 39%;
    width: 552px;
    min-height: 499px;
    z-index: 1;
  }
}

.hero__link {
  width: 100%;
  padding: 10px 5px;
  line-height: 18px;
  font-size: 14px;
  min-height: 40px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .hero__link {
    width: 244px;
    font-size: 20px;
    line-height: 20px;
    padding: 17px 5px;
    z-index: 1;
    margin-left: 90px;
    margin-bottom: 526px;
  }
}
@media (min-width: 1440px) {
  .hero__link {
    margin-left: 80px;
    margin-bottom: 183px;
  }
}

.advantages {
  display: flex;
  flex-direction: column;
}

.advantages__container {
  min-width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .advantages__container {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .advantages__container {
    width: 1220px;
  }
}

.advantages__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: black;
  margin: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 60px;
    line-height: 60px;
    grid-column: 1/-1;
    margin-bottom: 33px;
  }
}
@media (min-width: 768px) {
  .advantages__title {
    margin-bottom: 35px;
  }
}

.advantages__list {
  list-style: none;
  font-family: "Lato", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  margin-bottom: 49px;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 250px 250px;
    column-gap: 135px;
    row-gap: 17px;
    counter-reset: index;
    margin-bottom: 25px;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 77px;
    margin-bottom: 50px;
  }
}

.advantages__item {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin: 0;
  margin-bottom: 20px;
  padding-left: 80px;
  position: relative;
  min-height: 60px;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .advantages__item::before {
    top: 40px;
    width: 80px;
    height: 80px;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    font-size: 16px;
    line-height: 24px;
    padding-top: 151px;
    padding-bottom: 57px;
  }
  .advantages__item::after {
    counter-increment: index;
    content: counters(index, ".", decimal) " ";
    position: absolute;
    font-size: 280px;
    line-height: 280px;
    top: 0;
    right: 0;
